"use client"

import { useState, useEffect } from "react"
import Image from "next/image"
import { DashboardLayout } from "@/components/layout/DashboardLayout"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Progress } from "@/components/ui/progress"
import { GlobeIcon, BookOpenIcon, PlayIcon } from "lucide-react"
import { toast } from "sonner"

interface Lesson {
  id: string
  moduleId: string
  title: string
  content: string
  order: number
  duration: number
}

interface Module {
  id: string
  language: 'german' | 'arabic'
  title: string
  description: string
  image: string
  lessons: Lesson[]
  progress: number
}

export default function LanguagePage() {
  const [activeTab, setActiveTab] = useState<'german' | 'arabic'>('german')
  const [modules, setModules] = useState<Module[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetchModules(activeTab)
  }, [activeTab])

  const fetchModules = async (language: string) => {
    try {
      setLoading(true)
      const response = await fetch(`/api/language?language=${language}`)
      const data = await response.json()
      setModules(data)
    } catch (error) {
      toast.error("Failed to fetch language modules")
    } finally {
      setLoading(false)
    }
  }

  const startLesson = async (moduleId: string, lessonId: string) => {
    try {
      const response = await fetch('/api/language', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          moduleId,
          lessonId,
          action: 'start_lesson',
        }),
      })

      if (!response.ok) {
        throw new Error('Failed to start lesson')
      }

      const lesson = await response.json()
      toast.success(`Started lesson: ${lesson.title}`)
      // In a real app, we would navigate to the lesson content here
    } catch (error) {
      toast.error("Failed to start lesson")
    }
  }

  const updateProgress = async (moduleId: string, progress: number) => {
    try {
      const response = await fetch('/api/language', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          moduleId,
          progress,
          action: 'update_progress',
        }),
      })

      if (!response.ok) {
        throw new Error('Failed to update progress')
      }

      const updatedModule = await response.json()
      setModules(prev =>
        prev.map(module =>
          module.id === updatedModule.id ? updatedModule : module
        )
      )
    } catch (error) {
      toast.error("Failed to update progress")
    }
  }

  if (loading) {
    return (
      <DashboardLayout>
        <div className="flex items-center justify-center h-full">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900" />
        </div>
      </DashboardLayout>
    )
  }

  return (
    <DashboardLayout>
      <div className="space-y-6">
        <Tabs defaultValue="german" className="space-y-6" onValueChange={(value) => setActiveTab(value as 'german' | 'arabic')}>
          <div className="flex items-center justify-between">
            <TabsList>
              <TabsTrigger value="german" className="space-x-2">
                <GlobeIcon className="h-4 w-4" />
                <span>German</span>
              </TabsTrigger>
              <TabsTrigger value="arabic" className="space-x-2">
                <GlobeIcon className="h-4 w-4" />
                <span>Arabic</span>
              </TabsTrigger>
            </TabsList>
          </div>

          {['german', 'arabic'].map((lang) => (
            <TabsContent key={lang} value={lang} className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {modules.map((module) => (
                  <Card key={module.id} className="overflow-hidden">
                    <div className="relative h-48">
                      <Image
                        src={module.image}
                        alt={module.title}
                        fill
                        className="object-cover"
                      />
                    </div>
                    <div className="p-6">
                      <h3 className="text-xl font-semibold mb-2">{module.title}</h3>
                      <p className="text-gray-500 mb-4">{module.description}</p>
                      <div className="space-y-4">
                        <div className="flex items-center justify-between text-sm">
                          <span>{module.progress}% Complete</span>
                          <span>{module.lessons.length} Lessons</span>
                        </div>
                        <Progress value={module.progress} className="h-2" />
                        <div className="flex justify-between">
                          <Button 
                            variant="outline" 
                            size="sm"
                            onClick={() => toast.info("Viewing module details...")}
                          >
                            <BookOpenIcon className="h-4 w-4 mr-2" />
                            View Details
                          </Button>
                          <Button 
                            size="sm"
                            onClick={() => {
                              const firstLesson = module.lessons[0]
                              if (firstLesson) {
                                startLesson(module.id, firstLesson.id)
                              }
                            }}
                          >
                            <PlayIcon className="h-4 w-4 mr-2" />
                            Start Learning
                          </Button>
                        </div>
                      </div>
                    </div>
                  </Card>
                ))}
              </div>
            </TabsContent>
          ))}
        </Tabs>
      </div>
    </DashboardLayout>
  )
}
