"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import {
  MonitorIcon,
  RadioIcon,
  HeadphonesIcon,
  FolderIcon,
  GlobeIcon,
  MessageSquareIcon,
  HandIcon,
} from "lucide-react"

const navigation = [
  { name: "Dashboard", href: "/dashboard", icon: MonitorIcon },
  { name: "Student Monitoring", href: "/monitoring", icon: MonitorIcon },
  { name: "Broadcast", href: "/broadcast", icon: RadioIcon },
  { name: "Audio/Conferencing", href: "/conferencing", icon: HeadphonesIcon },
  { name: "File Distribution", href: "/files", icon: FolderIcon },
  { name: "Language Modules", href: "/language", icon: GlobeIcon },
  { name: "Private Messaging", href: "/messages", icon: MessageSquareIcon },
  { name: "Raise Hand Alerts", href: "/alerts", icon: HandIcon },
]

export function Sidebar() {
  const pathname = usePathname()

  return (
    <div className="flex h-screen w-64 flex-col bg-[#1a365d] text-white">
      <div className="p-4">
        <h1 className="text-xl font-bold">Educational Platform</h1>
      </div>
      <nav className="flex-1 space-y-1 px-2 py-4">
        {navigation.map((item) => {
          const isActive = pathname === item.href
          return (
            <Link
              key={item.name}
              href={item.href}
              className={cn(
                "group flex items-center px-2 py-2 text-sm font-medium rounded-md",
                isActive
                  ? "bg-[#2d4a77] text-white"
                  : "text-gray-300 hover:bg-[#2d4a77] hover:text-white"
              )}
            >
              <item.icon
                className={cn(
                  "mr-3 h-5 w-5",
                  isActive
                    ? "text-white"
                    : "text-gray-400 group-hover:text-white"
                )}
              />
              {item.name}
            </Link>
          )
        })}
      </nav>
    </div>
  )
}
